import 'react-minimal-side-navigation/lib/ReactMinimalSideNavigation.css';
import 'semantic-ui-css/semantic.min.css';
import {Navigation} from 'react-minimal-side-navigation';
import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import { Icon } from 'semantic-ui-react';


const Bar = styled.div`
  position: sticky;
  top: 200px;
  width: 15%;
  height: 100%;
  /* position: fixed;
  left: 19rem;
  top: 12rem;
  transform: translate(1em, 12rem); */
`;
 const SideNavbar = () => {
  const navigate = useNavigate();
    return (
      <>
        <Bar>
        <Navigation
          activeItemId="/main"
          onSelect={({ itemId }) => {
            if(itemId !== 'list'){
              navigate(itemId);
            }
          }}
          items={[
            {
              title: '',
              itemId: '/main',
              elemBefore: () => <Icon name="th large" style={{ fontSize: '1.2rem' }} />,
            },
            {
              title: '',
              itemId: '/list',
              elemBefore: () => <Icon name="tags" style={{ fontSize: '1.2rem' }} />,
              //subNav: user ? setArr : null,
              subNav: [
                {
                  title: 'Projects',
                  itemId: '/list1',
                },
                {
                  title: 'Members',
                  itemId: '/list2',
                },
              ],
            },
            {
              title: '',
              itemId: '/party',
              elemBefore: () => <Icon name="bookmark" style={{ fontSize: '1.2rem' }} />,
            },
            {
              title: ' ',
              itemId: '/guild',
              elemBefore: () => <Icon name="eye" style={{ fontSize: '1.2rem' }} />,
            },
          ]}
        />
      </Bar>
    </>
    )
};

export default SideNavbar;