import axios from 'axios';

function test() {
    // 'http://localhost:8080/test/testMethod'
    axios.get('http://localhost:8080/test/testMethod',{
        memberNo: 159967896
    })
    .then((response) => {
        console.log(response.data);		//     
    })
    .catch((error)=>{
        console.log(error);				// 
})
}

const Main = () => {
    
    return (
        <div>
            Main
            <button onClick={test}>Test</button>
        </div>
    );
}

export default Main;